import Masonry from 'react-masonry-css';
import Layout from '../components/Layout';

const buildGrid = () => {
  const grid = [];
  for (let i = 1; i <= 15; i++) {
    grid.push(
      <div className="grid-item">
        <img key={i} src={`../static/img/services-gallery/${i}.jpg`} alt="" />
      </div>
    );
  }

  return grid;
};

export default () => (
  <Layout>

    <section>
      <h1>Our Services</h1>

      <Masonry
        breakpointCols={3}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {buildGrid()}
      </Masonry>
    </section>

    <style jsx>
      {`
        section {
          flex: 1
          padding: 1rem
          text-align: center
        }

        .grid-sizer,
        .grid-item {
          width: 33.333%;
        }

        .grid-item {
          float: left;
        }

        .grid-item img {
          display: block;
          width: 100%;
        }
      `}
    </style>
  </Layout>
);



